.footer {
    background: black;
    height: 50vh;
    padding: 100px;
    color: white;
    width: 90vw;
}

.scrollTop {
    width: 10vw;
}

.scrollTop h1 {
    color: #1a18f7;
    font-size: 22px;
}

.footerLink {
    margin: 0 13px;
}

.footer a img {
    height: 16px;
    width: 16px;
}

.linkText {
    font-size: 18px;
    text-transform: uppercase;
}

.column-center {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.scrollTopContent:hover {
    cursor: pointer;
}

@media screen and (max-width: 425px) {
    .footerContainer .deptLogo {
        display: none;
    }
    .footerLink {
        font-size: 32px;
    }
    .footer {
        padding: 4px;
        width: 100vw;
        height: 70vh;
        justify-content: space-between !important;
    }
    .scrollTop {
        display: none !important;
    }
    .socialIcons {
        margin: 20px;
    }
    .footer a img {
        height: 24px;
        width: 24px;
    }
}